<template>
  <div>
    <form @submit="addTodooo">
      <input type="text" v-model="title" name="title" placeholder="add an item" />
      <input type="submit" value="submit" class="btn" />
    </form>
  </div>
</template>




<script>
import { v4 as uuidv4 } from "uuid";
export default {
  name: "AddTodo",
  data(){
      return {
          title: ''
      }
  },
  methods:{
    addTodooo(e){
        e.preventDefault();
        const newOne = {
            id: uuidv4(), //random custom id
            title: this.title,
            completed: false
        }
        //emit
        this.$emit('add-todo',newOne);
        this.title = '';
    }  
  }
}

</script>


<style scoped>
form {
  display: flex;
}
input[type="text"] {
  flex: 10;
  padding: 5px;
}
input[type="submit"] {
  flex: 2;
}
</style>