<template>
  <div id="app">
    <Header />
    <AddTodo v-on:add-todo="addTodoFinal" />
    {{msg}}>app
    <Todos v-bind:todos="todosarray" v-on:del-todo="deleteTodo" />
    <Todos v-bind:msg="msg" />
    <Todos v-bind:msg1="msg1" />
  </div>
</template>

<script>
import Header from "./components/layout/Header";
import Todos from "./components/Todos";
import AddTodo from "./components/AddTodo";
import axios from "axios";
export default {
  name: "App",
  components: {
    Todos,
    Header,
    AddTodo
  },
  data: function() {
    //function
    return {
      msg: "msg haha -----------",
      todosarray: [
        //array
        /*{
          id: 0,
          title: "todo 0",
          completed: false
        },
        {
          id: 1,
          title: "todo 1",
          completed: false
        },
        {
          id: 2,
          title: "todo 2",
          completed: true
        }*/
      ],
      msg1: "msg1"
    };
  },
  methods: {
    deleteTodo(id) {
      console.log("reached final method");
      this.todosarray = this.todosarray.filter(todo_ => todo_.id !== id);
    },
    addTodoFinal(newOne) {
      const { title, completed } = newOne;
      axios
        .post("https://jsonplaceholder.typicode.com/todos", {
          title,
          completed
        })
        .then(response => this.todosarray = [...this.todosarray, response.data])
        .catcher(err => console.log(err));

      //this.todosarray=[...this.todosarray,newOne];
    }
  },

  created() {
    axios
      .get("https://jsonplaceholder.typicode.com/todos?_limit=7") //returns a promise
      .then(response => (this.todosarray = response.data)) //handles the returned promise
      .catcher(error => console.log(error));
  }
};
</script>

