<template>
    <div class="hahaSTYLE" v-bind:class="{'is-complete':todo_.completed}">
        <p>
            <input type="checkbox" v-on:change="markComplete">
            {{todo_.title}}
        </p>
            <button @click="$emit('del-todo', todo_.id)" class="del">x</button> <!-- v-on:click="actionmethod" would be same as @click -->
            <!-- in del-todo, hyphen is convention -->
    </div>    
</template>

<script>
export default {
    name: "TodoItem",
    props: ["todo_"],
    methods:{
        markComplete(){
            console.log("clicked checkbox")
            this.todo_.completed = !this.todo_.completed;
        }
    }
}
</script>

